import { Calculator, DefenseCalculator } from "@/services/damageCalculator.js";
function sortedIndex(array, value) {
    var low = 0,
        high = array.length;

    while (low < high) {
        var mid = low + high >>> 1;
        if (array[mid].damageTaken > value) low = mid + 1;
        else high = mid;
    }
    return low;
}

const SET_BATTLE_RESULTS = function (state, payload) {
    let b1 = {}
    let selected = ''
    let total = {}
    const overall = []
    for (let pkmn of payload.pokemon) {

        if (!payload.defense) {
            b1[pkmn.data.slug] = Calculator(payload.tier, {
                ...pkmn.data, spread: {
                    stats: pkmn.spread,
                    nature: pkmn.nature
                }
            }, payload.weak, total)

        } else {
            pkmn.data.moves = pkmn.moves
            b1[pkmn.data.slug] = DefenseCalculator(payload.tier, {
                ...pkmn.data, spread: {
                    stats: pkmn.spread,
                    nature: pkmn.nature
                }
            }, payload.weak, total)
        }
        selected = pkmn.data.slug
    }

    for (const p in total) {
        for (const m in total[p]) {
            const i = sortedIndex(overall, total[p][m].damageTaken)
            overall.splice(i, 0, total[p][m])
        }
    }

    state.overall.battleResults = overall
    state.overall.battleSize = overall.length
    state.battleResults = b1;
    state.selectedAllyPokemon = selected;
    state.battleSize = Object.keys(b1[selected]).length;
}

const SET_SELECTED_ALLY_POKEMON = function (state, pokemon) {
    state.selectedAllyPokemon = pokemon
}

export default {
    SET_BATTLE_RESULTS,
    SET_SELECTED_ALLY_POKEMON
}