import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    name: "home",
    components: {
      desktop: () => import('../views/desktop/Home.vue'),
      mobile: () => import('../views/mobile/Home')
    }
  },
  {
    path: "/dex/:tier",
    name: "tier",
    components: {
      desktop: () => import('../views/desktop/TierView'),
      mobile: () => import('../views/mobile/TierView'),
    }
  },
  {
    path: "/dex/:tier/:pokemon",
    name: "pokemonviewer",
    components: {
      mobile: () => import("../views/mobile/PokemonViewer")
    }
  },
  {
    path: "/battlepark",
    name: "battlepark",
    components: {
      desktop: () => import("../views/desktop/BattlePark.vue"),
      mobile: () => import("../views/mobile/BattlePark")
    }
  },
  {
    path: "/battlepark/:tier/:id",
    name: "sharemyteam",
    components: {
      desktop: () => import("../views/desktop/TeamViewer.vue"),
      mobile: () => import("../views/desktop/TeamViewer.vue")
    }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
